import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApicallService } from 'src/app/services/CallApi/apicall.service';
import { GlobalService } from 'src/app/services/data/global.service';
import { ModalController } from "@ionic/angular";
import { ModalPage } from './../../pages/modal/modal.page';


@Component({
  selector: 'app-see-customer',
  templateUrl: './see-customer.page.html',
  styleUrls: ['./see-customer.page.scss'],
})
export class SeeCustomerPage implements OnInit {
   
  show = false;
   filterTerm!:string
   public Category: any = 'Customer' ;
  
  public customer_T:any=[
    {title:'Customer Name'},
    {title:'Father Name'},
    {title:'CNIC'},
    {title:'Mobile Number'},
    {title:'Address'}
  ]

  public See_Customer : any ={name:'',father_name:'', cnic:'',  cnic_img:'' , mobile_no: '' , address:'' }
  public data:any;

  constructor(public apiCall:ApicallService, private route: Router , public global: GlobalService, public modalController: ModalController) { }

  ngOnInit() {
    this.get_Customer();
  }

 // Get function of customer
  async get_Customer() {
    this.apiCall.api_get_customer();
    await this.global.Customer.subscribe(data => {
      console.log(data);
      this.See_Customer = data;
     });
   }
  // Delete function of customer
  async Delete_Customer(customer_id) {
    const x = {customer_id:customer_id}
    await this.apiCall.api_del_customer(x);
    this.get_Customer();
    console.log(customer_id);
  }

  Property(){
    this.route.navigate(['property'])
  }
  See_customer(){
    this.route.navigate(['see-customer'])
  }
  Customer(){
    this.route.navigate(['customers'])
  }
  Home(){
    this.route.navigate(['home'])
  }
  Refresh(){
    this.show= true;
    setTimeout(() => {
      this.show = false;
      this.apiCall.api_get_customer();
    }, 1500 ); 
   }


  async updateModal(data) {
    const modal = await this.modalController.create({
      component: ModalPage,
      cssClass: 'my-modal-class',
      componentProps: {
        'updateCustomer': data,
        'Category': this.Category,
      }
    });
    console.log('modal')
    return await modal.present();
  }

}
